/* Small Device = 320px */

@media only screen and (max-width: 479px) {
    .section-title h2 {
        font-size: 30px;
    }
    .about__video__text {
        padding: 70px 40px 70px 40px;
    }
    .team__item__img {
        float: none;
        margin-right: 0;
        margin-bottom: 30px;
        img {
            min-width: 100%;
        }
    }
    .hero__text .second-bg {
        margin-left: 0;
        margin-top: 20px;
    }
    .application-form .application__form__content {
        padding: 30px 20px;
    }
    .about-hero {
        margin-top: -74px;
    }
    .testimonial__content {
        padding: 70px 20px 70px;
    }
    .blog__hero__text {
        padding: 40px 45px 30px;
    }
    .search-model-form input {
        width: 100%;
        font-size: 24px;
    }
}