.header__logo_custom {
    @media only screen and (max-width: 768px) {
        text-align: left;
    }
    position: relative;
    z-index: 1;
    &::before {
        display: none;
        position: absolute;
        left: -400px;
        top: 0;
        width: 580px;
        height: 100%;
        background: $primary-color;
        content: '';
        z-index: -1;
        -webkit-clip-path: polygon(0% 0%, 100% 0%, 96% 100%, 0% 100%);
        clip-path: polygon(0% 0%, 100% 0%, 96% 100%, 0% 100%);
        @media only screen and(max-width: 768px) {
            width: 600px;
        }
    }
    a {
        display: inline-block;
        /*@media only screen and (max-width: 768px) {
            display: flex;
            justify-content: center;
        }*/
        img {
            max-height: 20vh;
            margin-left: -2vh;
            @media only screen and (max-width: 768px) {
                margin: 0;
                max-width: 50%;
            }
            @media only screen and (max-width: 768px) {
                margin: 0;
                max-width: 50%;
            }
        }
    }
}

.header__menu_custom {
    display: flex;
    align-items: center;
    padding: 28px 0px;
    text-align: center;
    justify-content: center;
    ul {
        li {
            list-style: none;
            display: inline-block;
            margin-right: 40px;
            position: relative;
            &.active {
                a {
                    &:after {
                        opacity: 1;
                    }
                }
            }
            &:hover {
                a {
                    &:after {
                        opacity: 1;
                    }
                }
                .dropdown {
                    top: 33px;
                    opacity: 1;
                    visibility: visible;
                }
            }
            &:last-child {
                margin-right: 0;
            }
            .dropdown {
                position: absolute;
                left: 0;
                top: 56px;
                width: 150px;
                background: $primary-color;
                text-align: left;
                padding: 2px 0;
                z-index: 9;
                opacity: 0;
                visibility: hidden;
                @include transition(all, .3s);
                li {
                    display: block;
                    margin-right: 0;
                    a {
                        font-size: 14px;
                        color: $white-color;
                        font-weight: 400;
                        padding: 8px 20px;
                        text-transform: capitalize;
                        &:after {
                            display: none;
                        }
                    }
                }
            }
            a {
                font-size: 14px;
                font-weight: 700;
                text-transform: uppercase;
                color: $white-color;
                display: block;
                padding: 6px 0;
                position: relative;
                &:after {
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 2px;
                    background: $primary-color;
                    content: '';
                    @include transition(all, .5s);
                    opacity: 0;
                }
            }
        }
    }
    @media only screen and (max-width: 991px) {
        display: none;
    }
}

.header__right_custom {
    display: flex;
    align-items: center;
    text-align: right;
    padding: 20px 0;
    @media only screen and (max-width: 991px) {
        display: none;
    }
}

.header__right__btn {
    display: inline-block;
}

.offcanvas-menu-wrapper {
    display: none;
}

.canvas__open_custom {
    display: none;
    @media only screen and (max-width: 991px) {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 22px;
        color: #ffffff;
        height: 35px;
        line-height: 34px;
        text-align: right;
        cursor: pointer;
        position: absolute;
        right: 25px;
        z-index: 9;
    }
    .fa {
        border: 1px solid;
        border-radius: 10px;
        padding: 10px;
    }
}

.section-title {
    text-align: center;
}

.blog-details {
    .proj_mgmt {
        margin-bottom: 20vh;
    }
}